import { OperatorConfig } from './types/operation-config';

const SIMPLE_ARRAY_OPERATOR_CONFIG = {
  contains: (value: any[], config: any[]) => {
    // True if all elements in config are present in value
    return config.every(val => value.includes(val));
  },
  isContainedBy: (value: any[], config: any[]) => {
    // True if all elements in value are present in config
    return value.every(val => config.includes(val));
  },
  overlap: (value: any[], config: any[]) => {
    // True if value and config have at least one common element
    return value.some(val => config.includes(val));
  },
  notContains: (value: any[], config: any[]) => {
    // True if not all elements in config are present in value
    return !config.every(val => value.includes(val));
  },
  notContainedBy: (value: any[], config: any[]) => {
    // True if not all elements in value are present in config
    return !value.every(val => config.includes(val));
  },
  notOverlap: (value: any[], config: any[]) => {
    // True if value and config have no common elements
    return !value.some(val => config.includes(val));
  },
};

export const OPERATOR_CONFIG: OperatorConfig = {
  string: {
    oneOf: (value: string, config: string[]) => config.includes(value),
    matchesAll: (value: string, config: string[]): boolean => {
      // True if all elements in config are present in value
      return config.every(pattern => {
        const regex = new RegExp(pattern);
        return regex.test(value);
      });
    },
    matchesAny: (value: string, config: string[]) => {
      // True if any element in config is present in value
      return config.some(pattern => {
        const regex = new RegExp(pattern);
        return regex.test(value);
      });
    },
  },
  number: {
    eq: (value: number, config: number) => value === config,
    gt: (value: number, config: number) => value > config,
    gte: (value: number, config: number) => value >= config,
    lt: (value: number, config: number) => value < config,
    lte: (value: number, config: number) => value <= config,
  },
  boolean: {
    eq: (value: boolean, config: boolean) => value === config,
  },
  'Array<string>': SIMPLE_ARRAY_OPERATOR_CONFIG,
  'Array<number>': SIMPLE_ARRAY_OPERATOR_CONFIG,
  'Array<boolean>': SIMPLE_ARRAY_OPERATOR_CONFIG,
};
