export const ProcessableEvents = [
  'rewardable.event',
  'game.events',
  'scheduled.event',
  'property.event',
  'admin.event',
] as const;

export type ProcessableEventType = (typeof ProcessableEvents)[number];

export const PlayerEvents = [
  'rewardable.event',
  'game.events',
  'scheduled.event',
  'property.event',
] as const;

export type PlayerEventType = (typeof PlayerEvents)[number];

export interface ProcessableEvent {
  namespace: string;
  dataType: string;
  eventId: string;
  body: Record<any, any>;
  externalId?: string;
  userId: string;
  /**
   * We banned this field in favor of `eventId`
   *
   * @deprecated Use `eventId` instead
   */
  id?: never;
}
