import { JSONPath } from 'jsonpath-plus';

export const getValue = (object: Record<string, any>, path: string): any => {
  if (!path || !path.startsWith('$')) {
    throw new Error('Invalid path provided for getValue');
  }

  const result = JSONPath({ path, json: object });
  return result.length > 0 ? result[0] : undefined; // Handle cases where no match is found
};
