import { FilterOperationDataType } from '@scrimmage/schemas';

export const detectPropertyDataType = (
  value: any,
): FilterOperationDataType | null => {
  if (Array.isArray(value)) {
    if (value.length === 0) {
      return null;
    }

    const elementType = typeof value[0];
    if (
      elementType === 'string' ||
      elementType === 'number' ||
      elementType === 'boolean'
    ) {
      return `Array<${elementType}>`;
    } else if (elementType === 'object') {
      return null;
    } else {
      throw new Error(
        'Unsupported value type. Only array of strings, numbers, or booleans are supported. Objects are ignored.',
      );
    }
  } else {
    return typeof value as FilterOperationDataType;
  }
};
