import { IScrimLangRequirementGroup } from '@scrimmage/schemas';
import { processProperty } from './process-property';
import { makeInlineKey } from './make-inline-key';

export const checkIfRequirementsFitsObject = (
  requirementGroups: IScrimLangRequirementGroup[],
  target: Record<string, any>,
  mode: 'properties' | 'event' = 'properties',
): boolean => {
  if (requirementGroups.length === 0) {
    return true;
  }
  for (const requirementGroup of requirementGroups) {
    let groupSuccess = true;
    for (const requirement of requirementGroup.elements) {
      const result = processProperty(
        target,
        requirement.filter,
        mode === 'properties'
          ? makeInlineKey(requirement.path)
          : requirement.path,
      );
      if (!result.success) {
        groupSuccess = false;
        break;
      }
    }
    if (groupSuccess) {
      return true;
    }
  }
  return false;
};
