import { JSONPath } from 'jsonpath-plus';

export const updateValue = <PatchValue = any>(
  object: Record<string, any>,
  path: string,
  patch: (currentValue: PatchValue) => PatchValue,
) => {
  if (!path || !path.startsWith('$')) {
    throw new Error('Invalid path provided for updateValue');
  }

  JSONPath({
    path,
    json: object,
    resultType: 'all', // This is crucial for in-place updates
    callback: result => {
      const updatedValue = patch(result.value);
      const parent = result.parent;
      const parentProperty = result.parentProperty;
      if (parent && parentProperty !== undefined) {
        parent[parentProperty] = updatedValue;
      }
    },
  });
  return object; // Return the modified object
};
