import { IScrimLangRequirementGroup } from './scrim-lang-requirement';
export const OrderByTypeOptions = ['DESC', 'ASC'] as const;
export type OrderByType = (typeof OrderByTypeOptions)[number];

export interface ISearchParams {
  query: string;
}

export interface IOrderParams {
  field: string;
  direction: OrderByType;
}

export interface IPaginationParams {
  page: number;
  limit: number;
}

export interface IFilterParams {
  propertyFilters: IScrimLangRequirementGroup[];
}

export interface IUserFetchParams {
  search?: ISearchParams;
  order?: IOrderParams[];
  pagination: IPaginationParams;
  filters?: IFilterParams;
}

export interface IUserExportParams {
  search?: ISearchParams;
  order?: IOrderParams[];
  filters?: IFilterParams;
  requestedProperties: string[];
  recipient: string;
  includeMetadata?: boolean;
}
