import { DifferenceEntry } from '../types/difference-entry';
import { getProperties } from './get-properties';

export const arrayEquals = <T>(arr1: T[], arr2: T[]): boolean => {
  return (
    arr1.length === arr2.length &&
    arr1.every((value, index) => value === arr2[index])
  );
};

export const getDifference = (obj1: any, obj2: any): DifferenceEntry[] => {
  const obj1Properties = getProperties(obj1);
  const obj2Properties = getProperties(obj2);

  const newProperties = obj2Properties.filter(
    prop => !obj1Properties.some(obj1Property => obj1Property.key === prop.key),
  );
  const removedProperties = obj1Properties.filter(
    prop => !obj2Properties.some(obj2Property => obj2Property.key === prop.key),
  );
  const changedProperties = obj1Properties
    .map(obj1Property => [
      obj1Property,
      obj2Properties.find(
        obj2Property => obj2Property.key === obj1Property.key,
      ),
    ])
    .filter(([, obj2Property]) => Boolean(obj2Property))
    .filter(
      ([obj1Property, obj2Property]) =>
        !arrayEquals(obj1Property.values, obj2Property.values),
    );

  const changedSimpleProperties = changedProperties.filter(
    ([obj1Property]) => !obj1Property.dataType.includes('Array'),
  );
  const changedArrayProperties = changedProperties.filter(([obj1Property]) =>
    obj1Property.dataType.includes('Array'),
  );

  return [
    ...newProperties.map(prop => ({
      path: prop.key,
      value: prop.values[0],
      previousValue: undefined,
    })),
    ...removedProperties.map(prop => ({
      path: prop.key,
      value: undefined,
      previousValue: prop.values[0],
    })),
    ...changedSimpleProperties.map(([obj1Property, obj2Property]) => ({
      path: obj1Property.key,
      value: obj2Property.values[0],
      previousValue: obj1Property.values[0],
    })),
    ...changedArrayProperties.map(([obj1Property, obj2Property]) => ({
      path: obj1Property.key,
      value: obj2Property.values,
      previousValue: obj1Property.values,
    })),
  ];
};
