import { JSONPath } from 'jsonpath-plus';

export const getEntries = (
  object: Record<string, any>,
  next: (
    path: string,
    value: (string | number | boolean)[],
    isArray: boolean,
  ) => void,
) => {
  if (object === null || object === undefined) {
    // Do nothing. We don't handle null and undefined for now.
    return;
  }

  const isSimpleValue = (value: any) =>
    typeof value !== 'object' || value === null;

  const paths = JSONPath({
    path: '$..*',
    json: object,
    resultType: 'path',
  }).filter((path: string) => {
    const value = JSONPath({ path, json: object, wrap: false });
    return isSimpleValue(value) || (Array.isArray(value) && value.length > 0);
  });

  const emittedPaths: string[] = [];

  paths.forEach(path => {
    const jsonPath = path.replace(/\[\d+]/g, '[*]');
    const values = JSONPath({ path: jsonPath, json: object });
    const nonObjectValues = values.filter(isSimpleValue);
    if (nonObjectValues.length === 0) {
      return;
    }

    const normalizedPath = jsonPath
      .replace(/\['(\w+)'\]/g, '.$1')
      .replace(/^\$/, '$');

    if (emittedPaths.includes(normalizedPath)) {
      return;
    }
    if (normalizedPath.endsWith('[*]')) {
      next(normalizedPath.replace(/\[\*]$/, ''), nonObjectValues, true);
    } else {
      next(normalizedPath, nonObjectValues, false);
    }
    emittedPaths.push(normalizedPath);
  });
};
