export function formatScrimlangKey(selectedKey: string, onlyLeaf = false) {
  if (!selectedKey) {
    return '';
  }
  if (onlyLeaf) {
    return selectedKey
      .split('.')
      .filter(el => !el.includes('$'))
      .slice(-1)[0];
  }
  return selectedKey
    .split('.')
    .filter(el => !el.includes('$'))
    .join(' > ');
}
