import {
  FILTER_OPERATOR_MAP,
  IScrimLangRequirementGroup,
  TextNode,
} from '@scrimmage/schemas';
import { formatScrimlangKey } from './format-key';

export const formatScrimLangRequirementDescription = (
  data: IScrimLangRequirementGroup[],
): TextNode[] => {
  const textNodes: TextNode[] = [];

  data.forEach((filter, filterIndex) => {
    if (filterIndex > 0) {
      textNodes.push({ text: 'or', bold: false, color: 'textSecondary' });
    }

    filter.elements.forEach(requirement => {
      const operator =
        FILTER_OPERATOR_MAP[requirement.filter.operator]?.extended ||
        requirement.filter.operator;

      const value = Array.isArray(requirement.filter.value)
        ? requirement.filter.value.join(', ')
        : requirement.filter.value.toString();
      textNodes.push({
        text: `${formatScrimlangKey(
          requirement.path,
          true,
        )} ${operator} ${value}`,
        bold: false,
        color: 'textPrimary',
      });
    });
  });

  return textNodes;
};

export const convertScrimLangRequirementTextNodesToHtml = (
  textNodes: TextNode[],
): string => {
  if (textNodes.length === 0) {
    return '';
  }

  const listItems = textNodes
    .map(node => {
      const boldTag = node.bold ? 'strong' : 'span';
      return node.text === 'or'
        ? `<${boldTag}>${node.text}</${boldTag}>`
        : `<li><${boldTag}>${node.text}</${boldTag}></li>`;
    })
    .join('');

  return `<ul>${listItems}</ul>`;
};
