import { IRewardablePropertyDTO } from '@scrimmage/schemas';
import { isSimpleValue } from './is-simple-value';
import { getEntries } from './get-entries';

export const getProperties = (object: Record<string, any>) => {
  const result: IRewardablePropertyDTO[] = [];
  getEntries(object, (path, values, isArray) => {
    const validValues = values.filter(value => isSimpleValue(value));
    if (validValues.length === 0) {
      return;
    }
    const firstValueDataType = typeof validValues[0] as
      | 'string'
      | 'number'
      | 'boolean';
    const scrimLangDataType = isArray
      ? (`Array<${firstValueDataType}>` as const)
      : firstValueDataType;
    const uniqueValues = [...new Set(validValues)];

    result.push({
      key: path,
      strict: false,
      type: '',
      uses: 0,
      dataType: scrimLangDataType,
      values: uniqueValues,
    });
  });
  return result;
};
