export const RedeemProviders = ['prizeout', 'webhook', 'tangible'] as const;
export type RedeemProvider = (typeof RedeemProviders)[number];

export const RedeemProviderTypes = [
  'prizeout',
  'currency conversion',
  'physical',
  'digital',
] as const;
export type RedeemProviderType = (typeof RedeemProviderTypes)[number];

export const RedeemProviderTypesMap: Record<
  RedeemProviderType,
  RedeemProvider
> = {
  prizeout: 'prizeout',
  'currency conversion': 'webhook',
  physical: 'tangible',
  digital: 'tangible',
};

export type RedeemEventType = 'external' | 'internal' | 'user';

export type RedeemTransactionStatus =
  | 'pending'
  | 'success'
  | 'failure'
  | 'postponed'
  | 'recoverable-failure';

export type RedeemLogEvent =
  | 'redeem.static'
  | 'redeem.static.success'
  | 'redeem.static.failure'
  | 'balance'
  | 'success'
  | 'failure';

export type RedeemLogType =
  | 'UserInitiallyTriggeredRedeemOption'
  | 'MessagePosted'
  | 'ExternalServerTriggeredEvent'
  | 'InternalErrorEvent'
  | 'LegacyEvent';
