import { IFilterConfigOperators } from '@scrimmage/schemas';
import { PropertyCheckResult } from '../types/property-check-result';
import { OPERATOR_CONFIG } from '../operator-config';
import { getValues } from './get-values';
import { detectPropertyDataType } from './detect-property-data-type';

export const processProperty = (
  entity: Record<string, any>,
  operatorConfig: IFilterConfigOperators,
  key: string,
): PropertyCheckResult => {
  const properties = getValues(entity, key);
  if (properties.length === 0) {
    // Property not found. Check failed.
    return {
      success: false,
      value: properties,
      property: key,
      operatorConfig: operatorConfig,
    };
  }
  const result = properties.some(property => {
    const propertyType = detectPropertyDataType(property);
    if (propertyType !== operatorConfig.type) {
      // Property type does not match operator type. Check failed.
      return false;
    }
    return OPERATOR_CONFIG[operatorConfig.type][operatorConfig.operator](
      property,
      operatorConfig.value,
    );
  });
  return {
    success: result,
    value: properties,
    property: key,
    operatorConfig: operatorConfig,
  };
};
