import { RewardablePropertyValue } from '../dto/rewardable';

export type UserProperties = {
  ['$.questCompletions']: Record<string, number>;
  ['$.questCompletedAt']: Record<string, string>;
  ['$.questsCompleted']: number;
  ['$.questsCompletedIds']: string[];
  ['$.questsAcceptedIds']: string[];
  ['$.level']: number;
  ['$.experience']: number;
  ['$.goldTrophy']: number;
  ['$.featuredTrophy']: number;
  ['$.redeemAmount']: number;
  ['$.perkSlots']: number;
  ['$.bonusClaim.daily']: boolean;
  ['$.totalRewardsEarned.primaryReward']: number;
  ['$.totalRewardsRedeemed.redeemed']: number;
  ['$.tags']: string[];
} & {
  [key in `$.properties.${string}`]: RewardablePropertyValue;
} & {
  [key in `$.segments.${string}`]: boolean;
} & {
  [key in `$.externalRewards.${string}`]: number;
};

export type UserPropertiesKey = keyof UserProperties;

export const CustomUserPropertyTypes = ['string', 'number', 'boolean'] as const;
export type CustomUserPropertyValue = (typeof CustomUserPropertyTypes)[number];
export type CustomUserProperties = Record<string, CustomUserPropertyValue>;
