import { FilterOperationDataType } from '@scrimmage/schemas';

export const getDataTypeDefault = (dataType: FilterOperationDataType) => {
  switch (dataType) {
    case 'number':
      return 0;
    case 'boolean':
      return false;
    case 'string':
      return '';
    case 'Array<boolean>':
      return [false];
    case 'Array<number>':
      return [0];
    case 'Array<string>':
      return [''];
    default:
      console.warn(`Unknown data type: ${dataType}`);
      return '';
  }
};
