export const createPropertyIfDoesntExist = (
  object: Record<string, any>,
  path: string,
  defaultValue: any,
): Record<string, any> => {
  if (!path || !path.startsWith('$.')) {
    throw new Error(
      'Invalid JSONPath provided for createPropertyIfDoesntExistJsonPath',
    );
  }

  // Deep-clone the object
  const newObj = JSON.parse(JSON.stringify(object));

  // Remove the '$.' at the beginning of the path
  const normalizedPath = path.slice(2);
  const segments = normalizedPath.split('.').filter(segment => segment !== '');

  const createRecursive = (obj: any, segs: string[]): void => {
    if (segs.length === 0) {
      return;
    }

    const currentSegment = segs[0];
    const remainingSegments = segs.slice(1);

    if (currentSegment.endsWith('[*]')) {
      // Handle array wildcard
      const arrayKey = currentSegment.slice(0, -3);
      if (!Array.isArray(obj[arrayKey])) {
        obj[arrayKey] = [];
      }

      if (remainingSegments.length === 0) {
        obj[arrayKey].push(defaultValue);
      } else {
        if (obj[arrayKey].length === 0) {
          obj[arrayKey].push({});
        }
        obj[arrayKey].forEach((item: any) =>
          createRecursive(item, remainingSegments),
        );
      }
    } else if (/\[\d+\]$/.test(currentSegment)) {
      // Handle array index
      const match = currentSegment.match(/(.*?)\[(\d+)\]$/);
      if (match) {
        const arrayKey = match[1];
        const index = parseInt(match[2], 10);

        if (!Array.isArray(obj[arrayKey])) {
          obj[arrayKey] = [];
        }

        while (obj[arrayKey].length <= index) {
          obj[arrayKey].push({});
        }

        createRecursive(obj[arrayKey][index], remainingSegments);
      }
    } else {
      // Handle standard property
      if (typeof obj[currentSegment] === 'undefined') {
        obj[currentSegment] =
          remainingSegments.length === 0 ? defaultValue : {};
      }
      createRecursive(obj[currentSegment], remainingSegments);
    }
  };

  createRecursive(newObj, segments);
  return newObj;
};
